import React, {Component} from 'react';
import ToDoList from '../to-do-list';
import Statistic from '../statistic';
import ToDoForm from '../to-do-form';
import './app.css';


export default class App extends Component{
    state = {
        maxId : 4,
        todos : [
            {
                text: 'listen a new audio book',
                isDone: false,
                id: 1,
                isImportant: false
            },
            {
                text: 'Repair a car',
                isDone: true,
                id: 2,
                isImportant: false
            },
            {
                text: 'Have a lanch',
                isDone: false,
                id: 3,
                isImportant: true
            },
            {
                text: 'look a new film',
                isDone: true,
                id: 4,
                isImportant: false
            }
        ]
    }
    onItemClick= id =>{
       const todos2 = [...this.state.todos];
        todos2.forEach(el => {
            if(el.id === id){
                el.isDone = !el.isDone;
            }
        });
        this.setState({ todos : todos2 })
    }

    onImportantClick= id =>{
        const todos2 = [...this.state.todos];
        todos2.forEach(el => {
            if(el.id === id){
                el.isImportant = !el.isImportant
                ;
            }
        });
        this.setState({ todos : todos2 })
    }

    onAddItem = () =>{
        console.log('add item');
        const todos = [...this.state.todos];
        todos.unshift(this.genereteItem());
        this.setState({todos})
    }

    genereteItem(text = 'defult'){
        const id = this.state.maxId + 1;
        this.setState({maxId : id});
        return{
            id,
            text,
            isDone: false,
            isImportant: false
        }
    }

    onDeleteItemClick = id =>{
        const todos =[...this.state.todos].filter(el => el.id !== id);
        this.setState({todos})
    }

    render(){
    const allTasks = this.state.todos.length;
    let doneTasks = this.state.todos.reduce((counter,{isDone}) => counter+=isDone? 1: 0, 0)
        
    return(
        <div className ="app">
            <Statistic 
            all ={allTasks} 
            done ={doneTasks} />
            <ToDoForm
            onAddItem ={this.onAddItem}
            />
            <ToDoList 
                data ={this.state.todos}
                onItemClick = {this.onItemClick}
                onImportantClick ={ this.onImportantClick }
                onDeleteItemClick ={this.onDeleteItemClick}
                />
        </div>    
    );
        
    }
}
